
import {Options, Vue} from 'vue-class-component';
import MinecraftServerWidget from '@/components/MinecraftServerWidget.vue';

@Options({
  components: {
    MinecraftServerWidget
  }
})
export default class MinecraftServerWidgetList extends Vue {
  serverList = ['RLCraft', 'Automaton', 'Vanilla 1.18', 'Origins'];

  async mounted() {
    fetch('http://localhost:3000/servers')
        .then(res => res.json())
        .then(data => this.serverList = data)
        .catch(err => console.log('ERROR: ' + err.message));
  }
}
